.button {
  font-family: $oswald400;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  font-size: 16px;
  line-height: 20px;
  color: $color-bg-white;
  background-color: $color-bg-green;
  text-decoration: none;
  text-transform: uppercase;
  cursor: pointer;
}
